.card {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 0;
  overflow: hidden;
  width: 100%;
  height: 100%;

  & img {
    width: 50%;
    height: 50%;
    border-radius: 50%;
  }

  &__blur {
    position: absolute;
    z-index: 1;
    opacity: 0.4;
    filter: blur(10px);
    transform: translateY(10px) scale(1.25);
  }

  &__img {
    position: relative;
    z-index: 0;
  }

  .nav-link {
    text-decoration: none;
    color: #c5e6eb;
    font-size: 14px;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    transition: color 1s ease;

    &:hover {
      color: #fff;
    }
  }
}
